import {
  ComponentPropsWithoutRef,
  forwardRef,
  Ref,
  useEffect,
  useRef,
  useState,
  UIEvent,
} from "react";
import cx from "classnames";
import styles from "./Page.module.scss";
import { useMergeRefs } from "../../hooks";

/**
 * Props for the PageContent component
 * @extends ComponentPropsWithoutRef<"main">
 */
export type PageContentProps = ComponentPropsWithoutRef<"main">;

/**
 * PageContent component for the main content area of a page.
 *
 * Features:
 * - Semantic main element for page content
 * - Virtual scrolling support for long content
 * - Automatic scroll synchronization
 * - Responsive design
 * - Accessibility support with proper main landmark
 * - Scroll position management
 *
 * @example
 * <Page.Content>
 *   <Text>Main page content goes here</Text>
 *   <Button>Action Button</Button>
 * </Page.Content>
 */
export const PageContent = forwardRef(
  (props: PageContentProps, ref: Ref<HTMLAreaElement>) => {
    const { children, className, ...rest } = props;
    const classNames = cx([styles["content"]], className);
    const mainRef = useRef<HTMLAreaElement>(null);
    const virtualScrollRef = useRef<HTMLDivElement>(null);
    const blocker = useRef(true);
    const [leftOffset, setLeftOffset] = useState(
      (mainRef.current?.offsetLeft ?? 0) +
        (mainRef.current?.offsetWidth ?? 0) -
        9,
    );
    const [contentHeight, setContentHeight] = useState(
      mainRef.current?.offsetHeight,
    );

    // Updates scroll position
    useEffect(() => {
      if (typeof window === "undefined") return;
      if (!blocker.current) return;
      function updatePosition() {
        const newValue = window.scrollY;

        virtualScrollRef.current?.scrollTo(0, newValue ?? 0);
      }
      window?.addEventListener("scroll", updatePosition, { passive: true });

      return () => {
        window?.removeEventListener("scroll", updatePosition);
      };
    }, []);

    // Updates virtual scroll properties when main resizes
    useEffect(() => {
      if (mainRef.current) {
        const observer = new ResizeObserver(() => {
          if (!mainRef.current) return;
          setLeftOffset(
            mainRef.current.offsetLeft + mainRef.current.offsetWidth - 9,
          );
          setContentHeight(mainRef.current.offsetHeight);
        });

        observer.observe(mainRef.current);

        // Cleanup function
        return () => {
          observer.disconnect();
        };
      }
    }, []);

    function scrollBody(e: UIEvent<HTMLElement>) {
      if (typeof window === "undefined") return;
      if (blocker.current) return;
      window.scrollTo(0, (e.target as HTMLDivElement).scrollTop);
    }

    return (
      <main className={classNames} ref={useMergeRefs([mainRef, ref])} {...rest}>
        <div className={styles["content-inner"]}>{children}</div>
        <div
          className={styles["virtual-scroll-container"]}
          ref={virtualScrollRef}
          style={{
            left: leftOffset,
          }}
          onScrollCapture={scrollBody}
          onMouseEnter={() => {
            blocker.current = false;
          }}
          onMouseLeave={() => {
            blocker.current = true;
          }}
        >
          <div style={{ width: 1, height: contentHeight }} />
        </div>
      </main>
    );
  },
);

PageContent.displayName = "PageContent";
