import { ComponentPropsWithoutRef, forwardRef, Ref } from "react";
import { useLayoutPropsUtil } from "../../internal/hooks";
import { LayoutUtilProps } from "../../types";
import cx from "classnames";
import styles from "./Page.module.scss";

/**
 * Props for the PageFooter component
 * @extends ComponentPropsWithoutRef<"div">
 * @extends LayoutUtilProps
 */
export type PageFooterProps = ComponentPropsWithoutRef<"div"> & LayoutUtilProps;

/**
 * PageFooter component for creating page footers with layout utilities.
 *
 * Features:
 * - Semantic footer element
 * - Layout utility props support
 * - Responsive design
 * - Accessibility support with proper footer landmark
 * - Flexible content positioning
 *
 * @example
 * <Page.Footer>
 *   <Text>Footer content with layout utilities</Text>
 * </Page.Footer>
 */
export const PageFooter = forwardRef(
  (props: PageFooterProps, ref: Ref<HTMLDivElement>) => {
    const { layoutStyles, componentProps } = useLayoutPropsUtil(props);
    const { children, className, style, ...rest } = componentProps;

    const classNames = cx([styles["content-footer"]], className);
    const styleCombined = {
      ...style,
      ...layoutStyles,
    };
    return (
      <footer className={classNames} ref={ref} style={styleCombined} {...rest}>
        {children}
      </footer>
    );
  },
);
PageFooter.displayName = "PageFooter";
