import {
  ComponentPropsWithoutRef,
  forwardRef,
  Ref,
  useContext,
  useLayoutEffect,
  useState,
} from "react";
import { core } from "@servicetitan/hammer-token";
import cx from "classnames";
import styles from "./Page.module.scss";
import Dialog from "../Dialog";
import { PageContext, PageContextProps } from "./PageContext";

/**
 * Props for the PagePanel component
 * @extends ComponentPropsWithoutRef<"div">
 */
export type PagePanelProps = ComponentPropsWithoutRef<"div"> & {
  /**
   * Whether the panel is open
   * @default true
   */
  open?: boolean;
  /**
   * Size of the panel
   * @default "medium"
   */
  size?: "small" | "medium" | "large";
  /**
   * Whether to remove default padding from the panel
   * @default false
   */
  noPadding?: boolean;
};

/**
 * PagePanel component for creating slide-out panels or dialogs.
 *
 * Features:
 * - Responsive design with dialog fallback on mobile
 * - Multiple size variants (small, medium, large)
 * - Optional padding control
 * - Persistent or controlled open state
 * - Automatic mobile dialog conversion
 * - Keyboard navigation and accessibility support
 * - Smooth animations and transitions
 *
 * @example
 * <Page.Panel open={isOpen} size="medium">
 *   <Text variant="headline" el="h3">Panel Content</Text>
 *   <Button onClick={() => setIsOpen(false)}>Close</Button>
 * </Page.Panel>
 */
export const PagePanel = forwardRef(
  (props: PagePanelProps, ref: Ref<HTMLDivElement>) => {
    const {
      children,
      open,
      size = "medium",
      noPadding,
      style,
      className,
      ...rest
    } = props;
    const classNames = cx([styles["panel"]], [styles[size]], className, {
      [styles["open"]]: typeof open === "undefined" ? true : open,
      [styles["persistent"]]: typeof open === "undefined",
      [styles["noPadding"]]: noPadding,
    });

    const { pageWidth } = useContext<PageContextProps>(PageContext);

    const [isMobile, setIsMobile] = useState<boolean>(false);

    useLayoutEffect(() => {
      const breakpointWidth = +(
        core.primitive?.BreakpointLg?.value.replace("px", "") ?? 0
      );
      const innerWidth = pageWidth ?? 0;
      setIsMobile(innerWidth < breakpointWidth);
    }, [pageWidth]);

    if (typeof open !== "undefined" && isMobile) {
      return (
        <Dialog open={open}>
          <Dialog.Content>
            <div
              className={classNames}
              aria-hidden={!open}
              aria-disabled={!open}
              ref={ref}
              {...rest}
            >
              {children}
            </div>
          </Dialog.Content>
        </Dialog>
      );
    }
    return (
      <div
        className={classNames}
        aria-hidden={!open}
        aria-disabled={!open}
        ref={ref}
        {...rest}
      >
        <div
          className={styles["panel-inner"]}
          /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
          tabIndex={typeof open !== "undefined" && open ? 0 : -1}
          style={style}
        >
          <div>{children}</div>
        </div>
      </div>
    );
  },
);
PagePanel.displayName = "PagePanel";
